<template>
  <div class="container-page">
      <div class="text-center blur-element" :class="{'active-blur':modal.show}">
          <h3>Tinggi Badan Kamu</h3>
          <h1 class="animated fadeInUp">
              <input v-model="inputtedTinggiBadan" type="number" min="1" max="999" placeholder="00" onfocus="blur()">
              <sub class="">cm</sub>
            </h1>
          <p class="animated fadeIn">Isi tinggi badan mu</p>
          <div class="animated fadeIn">
            <virtual-keyboard id="virtual-keyboard" :class="{'not-okay':inputtedTinggiBadan?false:true}"  :layout="virtualKeyboard.layout" :input="virtualKeyboard.selectedInput" :accept="nextPage"/>
          </div>
      </div>

      <!-- <modal id="modal" buttonCancelText="Tidak" buttonOkText="Ya" :callbackOk="nextPage" :callbackCancel="modalToggle" :show="modal.show"  ><h4>Yakin tinggi badan kamu <strong>{{inputtedTinggiBadan?inputtedTinggiBadan:0}}</strong> Cm  ?</h4></modal> -->
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'

import VirtualKeyboard from 'vue-touch-keyboard'
import Modal from '@/components/modal'

import VirtualKeyboardCustomLayout from '@/virtualKeyboardLayout'

export default {
    components:{
        'virtual-keyboard':VirtualKeyboard.component,
        Modal
    },
    data(){
        return {
            inputtedTinggiBadan : '',
            virtualKeyboard:{
                selectedInput : null,
                layout:null
            },
            modal:{
                show:false
            }
        }
    },
    methods:{
        ...mapActions('antropometri',['pushTinggiBadan','setPilihanTinggiBadan']),
        nextPage(){
            this.setPilihanTinggiBadan()
            this.$router.push('/choose/gender')
        },
        modalToggle(){
            this.modal.show = !this.modal.show
        }
    },
    computed:{
        ...mapGetters('antropometri',['getHasilPerhitungan', 'getOrangAntropometri'])
    },
    watch:{
        inputtedTinggiBadan(newValue){
            if(newValue.length <= 4){
                this.pushTinggiBadan(newValue)
            }
            else{
                this.inputtedTinggiBadan = newValue.substring(0,4)
            }
        }
    },
    created(){
        this.virtualKeyboard.layout = VirtualKeyboardCustomLayout.numeric
    },
    mounted(){
        this.virtualKeyboard.selectedInput = this.$el.querySelector('input')        
    }
}
</script>

