import Vue from 'vue'
import _ from 'lodash'

const state = {
    tinggiBadan : 0,
    beratBadan : [],
    usia: 0,
    jenisKelamin : 0,
    jenisAktivitas : 0,
    ukuranTerpilih:{
        tinggiBadan : 0,
        beratBadan : 0,
    },
    hasilPerhitungan : {
        indeksMassaTubuh: 0,
        kebutuhanKaloriHarian:0
    }
}

const getters = {
    getOrangAntropometri(state){
        return {
            tinggiBadan : state.ukuranTerpilih.tinggiBadan,
            beratBadan : state.ukuranTerpilih.beratBadan,
            usia : state.usia,
            jenisKelamin : state.jenisKelamin,
            jenisAktivitas : state.jenisAktivitas,
        }
    },
    getHasilPerhitungan(state){
        return state.hasilPerhitungan
    },
    getBeratBadanList(state){
        return state.beratBadan
    },
    getTinggiBadanList(state){
        return state.tinggiBadan
    },
    getPilihanBeratBadan(state){
        return state.ukuranTerpilih.beratBadan
    },
    getPilihanTinggiBadan(state){
        return state.ukuranTerpilih.tinggiBadan
    },
}

const mutations = {
    ADD_TINGGI_BADAN_LIST(state, payload){
        state.tinggiBadan = payload
    },
    ADD_BERAT_BADAN_LIST(state, payload){
        state.beratBadan.push(payload)
    },
    CLEAR_TINGGI_BADAN_LIST(state){
        Vue.set(state,'tinggiBadan',0)
    },
    CLEAR_BERAT_BADAN_LIST(state){
        Vue.set(state,'beratBadan',[])
    },
    SET_PILIHAN_TINGGI_BADAN(state, payload){
        Vue.set(state.ukuranTerpilih,'tinggiBadan',state.tinggiBadan)
    },
    SET_PILIHAN_BERAT_BADAN(state, payload){
        Vue.set(state.ukuranTerpilih,'beratBadan',payload)
    },
    SET_USIA(state, payload){
        Vue.set(state,'usia',payload)
    },
    SET_JENIS_KELAMIN(state, payload){
        Vue.set(state,'jenisKelamin',payload)
    },
    SET_JENIS_AKTIVITAS(state, payload){
        Vue.set(state,'jenisAktivitas',payload)
    },
    SET_HASIL_INDEKS_MASSA_TUBUH(state, payload){
        Vue.set(state.hasilPerhitungan,'indeksMassaTubuh',payload)
    },
    SET_HASIL_KEBUTUHAN_KALORI_HARIAN(state, payload){
        Vue.set(state.hasilPerhitungan,'kebutuhanKaloriHarian',payload)
    },
}

const actions = {
    pushTinggiBadan({commit}, payload){
        commit('ADD_TINGGI_BADAN_LIST', payload)
    },
    pushBeratBadan({commit}, payload){
        // console.log(`Berat Badan : ${payload}`);
        commit('ADD_BERAT_BADAN_LIST', payload)
    },
    clearTinggiBadanList({commit,state}){
        commit('CLEAR_TINGGI_BADAN_LIST')
    },
    clearBeratBadanList({commit,state}){
        commit('CLEAR_BERAT_BADAN_LIST')
    },
    setPilihanTinggiBadan({commit, state}){
        commit('SET_PILIHAN_TINGGI_BADAN')
        // let uniqueArray = _.countBy(state.tinggiBadan)

        // let newArray = []
        // for(let item in uniqueArray){
        //   let newObject = {
        //     number : item,
        //     count : uniqueArray[item]
        //   }
          
        //   newArray.push(newObject)
        // }

        // let sortedArray = newArray.sort((a,b)=> a.count-b.count)

        // if(sortedArray){
        //     // get the most number frequent and biggest value
        //     commit('SET_PILIHAN_TINGGI_BADAN', sortedArray[sortedArray.length-1].number)
        // }
    },
    setPilihanBeratBadan({commit, state}){
        let uniqueArray = _.countBy(state.beratBadan)

        let newArray = []
        for(let item in uniqueArray){
          let newObject = {
            number : item,
            count : uniqueArray[item]
          }
          
          newArray.push(newObject)
        }

        let sortedArray = newArray.sort((a,b)=> a.count-b.count)

        if(sortedArray.length){
            // get the most number frequent and biggest value
            commit('SET_PILIHAN_BERAT_BADAN', sortedArray[sortedArray.length-1].number)
        }
    },
    setPilihanBeratBadanNoArray({commit}, payload){
        commit('SET_PILIHAN_BERAT_BADAN', payload)
    },
    resetPilihanBeratDanTinggiBadan({commit,state}){
        commit('SET_PILIHAN_BERAT_BADAN', 0)
        commit('SET_PILIHAN_TINGGI_BADAN', 0)
    },
    setUsia({commit}, payload){
        commit('SET_USIA', payload)
    },
    setJenisKelamin({commit}, payload){
        commit('SET_JENIS_KELAMIN', payload)
    },
    setJenisAktivitas({commit}, payload){
        commit('SET_JENIS_AKTIVITAS', payload)
    },
    setHasilIndeksMassaTubuh({commit}, payload){
        commit('SET_HASIL_INDEKS_MASSA_TUBUH', payload)
    },
    setHasilKebutuhanKaloriHarian({commit}, payload){
        commit('SET_HASIL_KEBUTUHAN_KALORI_HARIAN', payload)
    },
    resetState({commit}){
        console.log('resetting');
        
        commit('SET_USIA', "0")
        commit('SET_PILIHAN_TINGGI_BADAN', "0")
        commit('SET_PILIHAN_BERAT_BADAN', '0')
        commit('SET_JENIS_KELAMIN', '0')
        commit('SET_JENIS_AKTIVITAS', '0')
        commit('SET_HASIL_INDEKS_MASSA_TUBUH', '0')
        commit('SET_HASIL_KEBUTUHAN_KALORI_HARIAN', '0')
        commit('CLEAR_BERAT_BADAN_LIST')
        // commit('CLEAR_TINGGI_BADAN_LIST')
    }
}

export default {
    namespaced:true,
    state,
    getters,
    mutations,
    actions
}