<template>
  <div class="container-page">
      <div class="text-center blur-element" :class="{'active-blur':modal.show}">
          <h3>Berapa Usiamu?</h3>
          <h1 class="">
              <input type="number" onfocus="blur()" v-model="inputtedUsia" min="1" placeholder="00" max="999" class="seamless-bg">
              <sub class="">Tahun</sub>
            </h1>
          <p class="">Pilih yang benar ya</p>
          <div>
            <virtual-keyboard id="virtual-keyboard" :class="{'not-okay':inputtedUsia?false:true}" :layout="virtualKeyboard.layout" :input="virtualKeyboard.selectedInput" :accept="nextPage"/>
          </div>
      </div>

      <!-- <modal id="modal" buttonCancelText="Tidak" buttonOkText="Ya" :callbackOk="nextPage" :callbackCancel="modalToggle" :show="modal.show"  ><h4>Kamu berusia <strong>{{inputtedUsia?inputtedUsia:0}}</strong> tahun ?</h4></modal> -->
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'

import VirtualKeyboard from 'vue-touch-keyboard'
import Modal from '@/components/modal'

import VirtualKeyboardCustomLayout from '@/virtualKeyboardLayout'

export default {
    components:{
        'virtual-keyboard':VirtualKeyboard.component,
        Modal
    },
    data(){
        return {
            inputtedUsia : '',
            virtualKeyboard:{
                selectedInput : null,
                layout:null
            },
            modal:{
                show:false
            }
        }
    },
    methods:{
        ...mapActions('antropometri',['setUsia']),
        nextPage(){
            this.setUsia(this.inputtedUsia)
            this.$router.push('/choose/activity')
        },
        modalToggle(){
            this.modal.show = !this.modal.show
        }
    },
    computed:{
        ...mapGetters('antropometri',['getHasilPerhitungan', 'getOrangAntropometri'])
    },
    watch:{
        inputtedUsia(newValue){
            if(newValue.length <=3){ 
                this.inputtedUsia = newValue
            }
            else{
                this.inputtedUsia = newValue.substring(0,3)
            }
        }
    },
    created(){
        this.virtualKeyboard.layout = VirtualKeyboardCustomLayout.numeric
    },
    mounted(){
        this.virtualKeyboard.selectedInput = this.$el.querySelector('input')
    }
}
</script>

