<template>
  <div id="app" :class="{'no-navigation':noNavigationPane}">
    <navigation-pane v-if="!noNavigationPane"></navigation-pane>
    <div>
      <div id="particle-js"></div>
      <transition name="fade" mode="out-in">
        <router-view></router-view>  
      </transition>
      <img id="sub-image" class="sub-page-img-rektor animated hide" :class="{'no-navigation':noNavigationPane}" src="./assets/rektor.png" alt="">
    </div>

    <div class="information-display-container">
      <information-display/>
    </div>

  </div>
</template>

<script>
import 'particles.js'
import feather from 'feather-icons/dist/feather'
import {mapActions,mapGetters} from 'vuex'

import particlesJSConfig from '@/particleJsConfig/particlesjs-config.json'
import PahoMqttHandler from '@/helper/PahoMqttHandler'

import NavigationPane from '@/components/NavigationSide'
import InformationDisplay from './components/InformationDisplay'

export default {
  components:{
    NavigationPane,
    InformationDisplay
  },
  data(){
    return{
      noNavigationPane:true,
      subImageRektor:'',
      time:false,
      maksTinggiAlat:191 // cm
    }
  },
  methods:{
    ...mapActions('antropometri',['pushBeratBadan','pushTinggiBadan','resetState','clearBeratBadanList','clearTinggiBadanList','setPilihanBeratBadanNoArray']),
    updateBeratBadan(payloadValue){
      this.pushBeratBadan(parseFloat(payloadValue.payloadString).toFixed(2))
    },
    updateTinggiBadan(payloadValue){    
        
      this.pushTinggiBadan(parseFloat(this.maksTinggiAlat - payloadValue.payloadString).toFixed(2))
    },
    resetTimer(){
      clearTimeout(this.time)
      this.time = setTimeout(this.goToIdlePage, 20000) // 10000 => 10 Detik
      
    },
    goToIdlePage(){
      if(this.$route.path != '/'){
        
        if(this.isManualTypeApp){
          this.setPilihanBeratBadanNoArray(0)
        }

        this.clearTinggiBadanList()
        this.$router.push('/')
      }
    }
  },
  computed:{
    ...mapGetters('antropometri',['getBeratBadanList','getTinggiBadanList']),
    isManualTypeApp(){            
        if(process.env.VUE_APP_TYPE == 'auto'){
            return false
        }

        return true
    }
  },
  watch:{
    '$route'(){      
      if(this.$route.path != '/'){        
        this.subImageRektor.classList.replace('fadeOut','fadeIn')
        this.subImageRektor.classList.remove('hide')
        this.subImageRektor.classList.add('fadeIn')
      }
      else if(this.$route.path == '/'){
        this.subImageRektor.classList.replace('fadeIn','fadeOut')
        this.subImageRektor.classList.add('hide')
        this.subImageRektor.classList.add('fadeOut')
      }
      this.resetTimer()
    },
    // getBeratBadanList:{
    //   handler:function(value){
    //     if(value[value.length-1] && this.getBeratBadanList[value.length-1] > 100){
    //       console.log('There is no user');
    //     }
    //     else{
    //       this.resetTimer()
    //     }
    //   },
    //   deep:true
    // },
    getTinggiBadanList:{
      handler:function(value){
        if(value > 100){
          console.log('There is no user');
          // this.resetState()
          this.clearBeratBadanList()
        }
        else{
          console.log(`watcher : getTinggiBadan ${value}`);

          this.resetTimer()
        }
      },
      deep:true
    }
  },
  created(){
    let context = this

    let optionsMqtt = {
        clientId: 'client-app-',
        port: 8000,
    }
    
    let mqttHandlerBeratBadan = new PahoMqttHandler('broker.hivemq.com','/itbappsrektor/beratbadan', optionsMqtt)
    let mqttHandlerTinggiBadan = new PahoMqttHandler('broker.hivemq.com','/itbappsrektor/tinggi', optionsMqtt)

    mqttHandlerBeratBadan.subscribe((message) =>{
      context.updateBeratBadan(message)
    })

    mqttHandlerTinggiBadan.subscribe((message) =>{
      context.updateTinggiBadan(message)
    })
  },
  mounted(){
    window.particlesJS('particle-js', particlesJSConfig)

    window.onload = this.resetTimer
    window.onclick = this.resetTimer
    window.onmousemove = this.resetTimer

    feather.replace()

    this.subImageRektor = this.$el.querySelector('#sub-image')

    if(this.$route.path != '/'){        
      this.subImageRektor.classList.replace('fadeOut','fadeIn')
      this.subImageRektor.classList.remove('hide')
      this.subImageRektor.classList.add('fadeIn')
    }
  }
}
</script>

<style scoped lang="scss">
#app{
  position:relative;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns:15rem 1fr;

  &.no-navigation{
    grid-template-columns:1fr;
  }

  #particle-js{
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    z-index: -1;
  }
}

.sub-page-img-rektor{
  position: absolute;
  right: -27%;
  z-index: -1;
  height: 100vh;
  bottom: 0px;
  filter: grayscale(100%);
  opacity: 1;

  &.no-navigation{
    right:-21%;
  }

  &.hide{
    opacity: 0
  }

  @media screen and (min-width:1440px){
    right: -24%;
  }
}

.information-display-container{
  position: absolute;
  top :2rem;
  left: 2rem;

}


.fade-enter-active,
.fade-leave-active {
  transition-duration: 0.3s;
  transition-property: opacity;
  transition-timing-function: ease;
}

.fade-enter,
.fade-leave-active {
  opacity: 0
}
</style>
