<template>
  <div class="container-page">
      <div class="text-center">
          <h3>Apa Gendermu?</h3>
          <h5 class="">
              <div>
                  <input v-model="selectedGender" type="radio" name="gender" id="gender-1" value="pria">
                  <label for="gender-1">Pria</label>
              </div>
              <div>
                  <input v-model="selectedGender" type="radio" name="gender" id="gender-2" value="wanita">
                  <label for="gender-2">Wanita</label>
              </div>
          </h5>
          <p class="">Pilih salah satu</p>
      </div>

    <!-- <modal id="modal" buttonCancelText="Tidak" buttonOkText="Ya" :callbackOk="nextPage" :callbackCancel="cancel" :show="modal.show">
        <div>
            <h3> {{gender}} </h3>
        </div>
        <h4>Apakah pilihanmu sudah benar?</h4>
    </modal> -->
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import Modal from '@/components/modal'

import NextStepButton from './ButtonNextStep'

export default {
    components:{
        Modal,
        NextStepButton
    },
    data(){
        return {
            selectedGender:0,
            modal:{
                show:false
            }
        }
    },
    methods:{
        ...mapActions('antropometri',['setJenisKelamin']),
        nextPage(){
            this.$router.push('/choose/age')
        },
        cancel(){
            this.modal.show = false
        }
    },
    computed:{
        ...mapGetters('antropometri',['getHasilPerhitungan', 'getOrangAntropometri']),
        gender(){
            if(this.selectedGender == "pria"){
                return "Pria"
            }
            else if(this.selectedGender == "wanita"){
                return "Wanita"
            }
            else{
                return "Terjadi Kesalahan"
            }
        }
    },
    watch:{
        selectedGender(newValue){
            // this.modal.show = true
            this.setJenisKelamin(newValue)
            this.nextPage()
        }
    }
}
</script>

