<template>
  <div class="container-page">

      <article id="result-main">
        <div id="info-1" class="text-center">
            <h1 :class="{
                    'color-1':resultTextColorDeterminer() == 1?true:false,
                    'color-2':resultTextColorDeterminer() == 2?true:false,
                    'color-3':resultTextColorDeterminer() == 3?true:false,
                    'color-4':resultTextColorDeterminer() == 4?true:false,
                }">
                {{determineIndeksMassaTubuhCategory().message}}
            </h1>
            <h2>{{calculateIndeksMassaTubuh()}}</h2>
            <p class="animated delay-1s fadeIn">{{determineIndeksMassaTubuhCategory().subMessage}}</p>
        </div>
      </article>
      <article id="result-sub" class="hide">
        <div id="info-2" class="text-center">
            <h3>Yang Kamu Butuhkan</h3>
            <div class="recommendation-list">
                <article :class="{'selected':determineIndeksMassaTubuhCategory().level == 2}">
                    <span><p>Untuk tetap menjalani aktivitas normal</p></span>
                    <span>{{parseFloat(calculateRequiredCalories()).toFixed(1)}} kal<sub>/hari</sub></span>
                </article>
                <article :class="{'selected':determineIndeksMassaTubuhCategory().level > 2}">
                    <span><p>Untuk mengurangi berat badan</p></span>
                    <span>{{parseFloat(calculateRequiredCalories()-500).toFixed(1)}} kal<sub>/hari</sub></span>
                </article>
                <article :class="{'selected':determineIndeksMassaTubuhCategory().level && determineIndeksMassaTubuhCategory().level < 2}">
                    <span><p>Untuk meningkatkan berat badan</p></span>
                    <span>{{parseFloat(calculateRequiredCalories()+500).toFixed(1)}} kal<sub>/hari</sub></span>
                </article>
            </div>
        </div>
      </article>


      <next-step-button @click.native="clearTinggiBadan" to="/"/>
  </div>
</template>

<script>
import {mapActions,mapGetters} from 'vuex'
import JenisBebanAktivitas from '../helper/JenisBebanAktivitas'
import NextStepButton from './ButtonNextStep'

export default {
    components:{
        NextStepButton
    },
    data(){
        return  {
            resultMessage:[
                {level:0, message:'Masukan Tidak Valid', subMessage:'Terjadi kesalahan. Mohon bersabar'},
                {level:1, message:'Kamu Kurus', subMessage:'Ayo perbanyak asupan kalori harianmu'},
                {level:2, message:'Kamu Ideal', subMessage:'Selamat, ayo lihat asupan kalori harianmu'},
                {level:3, message:'Kamu Berlebih',subMessage:'Ayo kurangi asupan harianmu '},
                {level:4, message:'Kamu Obesitas', subMessage:'Semangat, kamu pasti bisa'},
            ]
        }
    },
    methods:{
        ...mapActions('antropometri',['setHasilIndeksMassaTubuh','setHasilKebutuhanKaloriHarian','setPilihanBeratBadanNoArray','clearTinggiBadanList']),
        genderPage(){
            this.$router.push('/choose/gender')
        },
        clearTinggiBadan(){
            this.clearTinggiBadanList()
            this.setPilihanBeratBadanNoArray(0)
        },
        resultTextColorDeterminer(){
            let result = this.determineIndeksMassaTubuhCategory()
            
            switch(result.level){
                case 1 :    
                        return 1
                case 2 :    
                        return 2
                case 3 :    
                        return 3
                case 4 :    
                        return 4    
                default:
                        return 0
            }
        },
        determineIndeksMassaTubuhCategory(){
            let indeksMassaTubuh = this.calculateIndeksMassaTubuh()

            if(indeksMassaTubuh && indeksMassaTubuh < 18.5){
                return this.resultMessage[1]
            }
            else if(indeksMassaTubuh >= 18.5 && indeksMassaTubuh <= 25){
                return this.resultMessage[2]
            }
            else if(indeksMassaTubuh > 25 && indeksMassaTubuh <= 27){
                return this.resultMessage[3]
            }
            else if(indeksMassaTubuh > 27){
                return this.resultMessage[4]
            }
            else{
                return this.resultMessage[0]
            }
        },
        calculateIndeksMassaTubuh(){
            if(this.getOrangAntropometri.beratBadan && this.getOrangAntropometri.tinggiBadan){
                let centimeter = this.centiToMeter(this.getOrangAntropometri.tinggiBadan)
                
                return parseFloat(this.getOrangAntropometri.beratBadan/(centimeter**2)).toFixed(1)
            }

            return 0
        },
        centiToMeter(centimeter){
            return centimeter / 100
        },
        calculateRequiredCalories(){
            let levelAktivitas = JenisBebanAktivitas.find(item =>{
                return item.level == this.getOrangAntropometri.jenisAktivitas
            })

            if(levelAktivitas){
                return levelAktivitas.valueWeight * this.calculateBMR()
            }
            return 0

        },
        calculateBMR(){
            if(this.getOrangAntropometri.jenisKelamin == 'pria'){
                return  66.4730 + (13.7516*this.getOrangAntropometri.beratBadan) + (5.0033*this.getOrangAntropometri.tinggiBadan) - (6.7550 * this.getOrangAntropometri.usia)
            }
            else if(this.getOrangAntropometri.jenisKelamin == 'wanita'){
                return  655.0955 + (9.5634*this.getOrangAntropometri.beratBadan) + (1.8496*this.getOrangAntropometri.tinggiBadan) - (4.6756 * this.getOrangAntropometri.usia)
            }
        }
    },
    computed:{
        ...mapGetters('antropometri',['getHasilPerhitungan', 'getOrangAntropometri'])
    },
    mounted(){

    }
}
</script>

