<template>
  <div>
      <section v-if="!noChart">
        <flickity ref="flickitySlider" :options="flickityOptions" @init="onInitFlickity">
            <div style="width:100%;height:100vh"  >
            <ayo-hitung-antropometri/> 
            </div>    
            <div style="width:100%;height:100vh">
                <chart-page-1/>
            </div>
            <div style="width:100%;height:100vh">
                <chart-page-2/>
            </div>
        </flickity>
      </section>
      <section v-else>
        <ayo-hitung-antropometri/> 
      </section>

      <router-link class="hidden-button" to="/measurement/weight">This is Hidden Button</router-link>
      <div class="next-step-manual-button" v-if="isManualTypeApp">
        <next-step-button to="/measurement/weight"/>
      </div>
  </div>
</template>

<script>
import { mapActions,mapGetters, mapState } from "vuex";
import Flickity from 'vue-flickity';
import AyoHitungAntropometri from './sub-landing-page-components/ayo-hitung-antropometri'
import ChartPage1 from './sub-landing-page-components/chart-page-1.vue'
import ChartPage2 from './sub-landing-page-components/chart-page-2.vue'
import NextStepButton from './ButtonNextStep'

export default {
    components:{
        AyoHitungAntropometri,
        ChartPage1,
        ChartPage2,
        NextStepButton,
        Flickity,
    },
    data(){
        return {
            noChart:true,
            flickityOptions:{
                draggable:true,
                pageDots:false,
                autoPlay:true,
                pauseAutoPlayOnHover:false,
                prevNextButtons:false,
                wrapAround:true,
                fade:true
            },
        }
    },
    methods:{
        ...mapActions('antropometri',['resetState','setPilihanBeratBadanNoArray','clearTinggiBadanList']),
        nextPage(){
            this.$router.push('/validation')
        },
        onInitFlickity(){
            this.$refs.flickitySlider.on('change', (index)=>{
                // console.log(index);
            })
        }
    },
    computed:{
        ...mapGetters('antropometri',['getTinggiBadanList','getBeratBadanList']),
        isManualTypeApp(){            
            if(process.env.VUE_APP_TYPE == 'auto'){
                return false
            }

            return true
        }
    },
    watch:{
        getTinggiBadanList:{
            handler:function(value){ 
                console.log('# Landing Page : Tinggi Watcher');
                console.log(value);
                
                if(parseFloat(value) > 100){
                    this.nextPage()
                }
            },
            deep:true
        },
        // getBeratBadan(value){
        //     if(value >= 25 && this.getTinggiBadan >= 100){
        //         this.nextPage()
        //     }
        // }
    },
    mounted(){
        console.log('State Reset');
        this.resetState()        
        if(this.isManualTypeApp){
            console.log('Manual Type Reset');
            this.clearTinggiBadanList()
            this.setPilihanBeratBadanNoArray(0)
            
        }
    }
}
</script>

<style lang="scss" scoped>

.next-step-manual-button{
    display: flex;
    justify-content: center;
    align-items: center;
}


.hidden-button{
    position: absolute;
    top:0px;
    left: 0px;
    opacity: 0;
}

</style>