import VueRouter from 'vue-router'

import LandingPage from './components/LandingPage.vue'

import MeasurementHeightWeightPage from './components/app-type/auto/MeasurementHeighWeightPage.vue'
import ValidationPage from './components/app-type/auto/ValidationPage.vue'

import MeasurementManualHeightPage from './components/app-type/manual/MeasurementManualHeightPage.vue'
import MeasurementManualWeightPage from './components/app-type/manual/MeasurementManualWeightPage.vue'

import ChooseGenderPage from './components/ChooseGenderPage.vue'
import ChooseAgePage from './components/ChooseAgePage.vue'
import ChooseActivityPage from './components/ChooseActivityTypePage.vue'
import MeasurementResultPage from './components/MeasurementResultPage.vue'

let routes = [
  {path:'/', component:LandingPage},
  {path:'/choose/gender', component:ChooseGenderPage},
  {path:'/choose/age', component:ChooseAgePage},
  {path:'/choose/activity', component:ChooseActivityPage},
  {path:'/measurement/result', component:MeasurementResultPage},
]

if(process.env.VUE_APP_TYPE == 'auto'){
  console.log("TYPE : Auto");

  routes.push({path:'/validation', component:ValidationPage})
  routes.push({path:'/measurement/weight', component:MeasurementHeightWeightPage})
}
else if(process.env.VUE_APP_TYPE == 'manual'){
  console.log("TYPE : Manual");
  
  routes.push({path:'/measurement/height', component:MeasurementManualHeightPage})
  routes.push({path:'/measurement/weight', component:MeasurementManualWeightPage})
}

// router
export default new VueRouter({
  mode:'hash',
  routes
})