<template>
  <div class="container-page">
      <div class="text-center">
          <h3>Hasilnya</h3>
          <div class="information">
              <article>
                  <transition name="slide-fade" mode="out-in">
                    <h1 :key="getPilihanBeratBadan" class="animated fadeInUp">{{parseFloat(getPilihanBeratBadan).toFixed(1)}}<sub class="">Kg</sub></h1>
                  </transition>
                <p>Berat</p>
              </article>
              <article>
                  <h2 style="margin:0rem 1rem"> & </h2>
              </article>
              <article>
                    <transition name="slide-fade" mode="out-in">
                        <h1 :key="getPilihanTinggiBadan" class="animated fadeInUp">{{parseFloat(getPilihanTinggiBadan).toFixed(1)}}<sub class="">Cm</sub></h1>
                    </transition>
                    <p>Tinggi</p>
              </article>
          </div>
      </div>

        <modal id="modal" buttonCancelText="Tidak" buttonOkText="Ya" :callbackOk="nextPage" :callbackCancel="cancelBackPage" :show="modal.show"  ><h4>Apa benar berat badanmu <strong>{{parseFloat(getPilihanBeratBadan).toFixed(1)}}</strong> kg dan tinggi badanmu <strong>{{parseFloat(getPilihanTinggiBadan).toFixed(1)}}</strong> cm?</h4></modal>

  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'

import Modal from '@/components/modal'

export default {
    components:{
        Modal
    },
    data(){
        return {
            modal:{
                show:false
            }
        }
    },
    methods:{
        ...mapActions('antropometri',['resetPilihanBeratDanTinggiBadan']),
        nextPage(){
            this.$router.push('/choose/gender')
        },
        cancelBackPage(){
            this.resetPilihanBeratDanTinggiBadan()
            this.$router.push('/validation')
        }
    },
    computed:{
        ...mapGetters('antropometri',['getPilihanBeratBadan','getPilihanTinggiBadan']),
    },
    mounted(){
        setTimeout(()=>{
            this.modal.show = true
        }, 3000)
    }
}
</script>

