<template>
  <div>
      <div class="backdrop animated hide" :class="{'fadeIn':show}">
      </div>
      <div class="modal">
        <div class="modal-content animated" tabindex="0" :class="{'fadeIn':show,'fadeOut':!show}">
            <div class="content-area">
                <slot>

                </slot>
            </div>
            <div class="button-area">
                <button type="button" @click="actionCancel">{{buttonCancelText}}</button>
                <button type="button" @click="actionOk">{{buttonOkText}}</button>
            </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
    props:{
        buttonCancelText: {
            type:String,
            default:'Cancel'
        },
        buttonOkText: {
            type:String,
            default:'Ok'
        },
        show:{
            type:Boolean,
            default:false
        },
        callbackOk:Function,
        callbackCancel:Function
    },
    methods:{
        actionOk(){
            this.callbackOk()
        },
        actionCancel(){
            this.callbackCancel()
        }
    },
    watch:{
        show(value){
            if(value){
                document.querySelector('div.modal-content').focus()
                
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/variable/_color.scss';

.backdrop{
    z-index:-1;
    top:0px;
    left: 0px;
    position:fixed;
    height: 100vh;
    width: 100vw;
    background:rgba(255, 255, 255, 0.5);
    transition:all ease 0.3s;

    &.hide{
        opacity: 0;
    }

    &.fadeIn,
    &.fadeIn + .modal{
        z-index:1000;
    }

    &.fadeIn + .modal{
        opacity: 1;
    }
}

.modal{
    transition:all ease 0.3s;
    position:fixed;
    left:50%;
    top:50%;
    transform:translate(-50%,-50%);
    opacity:0;
    z-index:-1;
}

.modal-content{
    z-index: inherit;
    width:30rem;
    background: white;
    padding-top:1.5rem;
    box-shadow: 0px 5px 10px -1px $color-primary;

    @media screen and (max-width: 512px) {
        width:calc(100vw - 2rem);
    }

    & > div.content-area{
        text-align:center;
        padding-left:1.5rem;
        padding-right:1.5rem;
    }

    & > div.button-area{
        margin-top:1rem;
        background: white;
        display: flex;
        justify-content: stretch;
        align-items: center;

        & > button {
            color:inherit;
            flex:1;
            font-weight: bold;
            font-size: 1.5rem;
            padding: 1rem;
            border:none;
            background: none;
            transition: all ease 0.3s;

            &:hover{
                opacity:0.8;
                cursor:pointer;
            }

            &:last-child{
                color:white;
                background: $color-accent;
            }

        }
    }
}

</style>