<template>
  <div class="filled-display">
        <h5 :class="{'hide':isEverythingOkay}">Informasi yang telah diisi</h5>
        <h6 :class="{'hide':isBeratBadanOkay}" >
            <span>Berat Badan</span> <span><strong>{{getOrangAntropometri.beratBadan}}</strong> Kg</span>
        </h6> 
        <h6 :class="{'hide':isTinggiBadanOkay}">
            <span>Tinggi Badan</span> <span><strong>{{getOrangAntropometri.tinggiBadan}}</strong> Cm</span>
        </h6> 
        <h6 :class="{'hide':isGenderOkay}"><span>Gender</span> <span style="text-transform: capitalize;"> <strong>{{getOrangAntropometri.jenisKelamin}}</strong></span></h6> 
        <h6 :class="{'hide':isUsiaOkay}"><span>Usia</span> <span> <strong>{{getOrangAntropometri.usia}}</strong> Tahun</span></h6> 
        <h6 :class="{'hide':isActivityOkay}"><span>Tingkat Aktivitas</span> <span> <strong>{{activityExtractor(this.getOrangAntropometri.jenisAktivitas)}}</strong></span></h6> 
    </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import JenisAktivitas from '../helper/JenisBebanAktivitas'

export default {
    methods:{
        ...mapActions('antropometri',['setPilihanBeratBadanNoArray']),
        activityExtractor(level){
            let result = JenisAktivitas.find((item)=>{
                return item.level == level
            })

            if(result){
                return result.subText
            }

            return "Masukan Tidak Valid"
        }
    },
    computed:{
        ...mapGetters('antropometri',['getOrangAntropometri']),
        isBeratBadanOkay(){
            return this.getOrangAntropometri.beratBadan != 0? false:true
        },
        isTinggiBadanOkay(){
            return this.getOrangAntropometri.tinggiBadan != 0? false:true
        },
        isGenderOkay(){
            return this.getOrangAntropometri.jenisKelamin == 'pria' ||this.getOrangAntropometri.jenisKelamin == 'wanita'? false:true
        },
        isUsiaOkay(){
            return this.getOrangAntropometri.usia != 0? false:true
        },
        isActivityOkay(){            
            return this.getOrangAntropometri.jenisAktivitas != 0 ? false:true
        },
        isEverythingOkay(){
            if(this.isBeratBadanOkay && this.isTinggiBadanOkay && this.isGenderOkay && this.isUsiaOkay && this.isActivityOkay){
                return true
            }

            return false
        }

    },
    updated(){
        console.log(this.getOrangAntropometri.beratBadan);
    }
    
}
</script>

<style scoped lang="scss">
@import '../assets/variable/_color.scss';

.filled-display{
    padding:0.5rem 2rem;
    
    & > h5{
        font-size: 1.4rem;
        transition: opacity ease 0.3s;
    }
    & > h6{
        font-size: 1.25rem;
        margin-bottom: 0.25rem;
        font-weight: lighter;
        transition: opacity ease 0.3s;

        & > span{
            &:last-child{
                & > strong{
                    color:$color-accent;
                }
            }
        }
    }
}

.hide{
    opacity: 0;
}

</style>