<template>
  <div class="container-page">
<div class="text-center blur-element" :class="{'active-blur':modal.show}">
          <h3>Berat Badan Kamu</h3>
          <h1 class="animated fadeInUp">
              <input v-model="inputtedBeratBadan" type="number" min="1" max="9999" placeholder="00" onfocus="blur()">
              <sub class="">Kg</sub>
            </h1>
          <p class="animated fadeIn">Isi berat badan mu</p>
          <div class="animated fadeIn">
            <virtual-keyboard id="virtual-keyboard" :class="{'not-okay':inputtedBeratBadan?false:true}" :layout="virtualKeyboard.layout" :input="virtualKeyboard.selectedInput" :accept="nextPage"/>
          </div>
      </div>

      <!-- <modal id="modal" buttonCancelText="Tidak" buttonOkText="Ya" :callbackOk="nextPage" :callbackCancel="modalToggle" :show="modal.show"  ><h4>Yakin berat badan kamu <strong>{{inputtedBeratBadan?inputtedBeratBadan:0}}</strong> Kg  ?</h4></modal> -->
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'

import VirtualKeyboard from 'vue-touch-keyboard'
import Modal from '@/components/modal'

import VirtualKeyboardCustomLayout from '@/virtualKeyboardLayout'

export default {
    components:{
        'virtual-keyboard':VirtualKeyboard.component,
        Modal
    },
    data(){
        return {
            inputtedBeratBadan : '',
            virtualKeyboard:{
                selectedInput : null,
                layout:null
            },
            modal:{
                show:false
            }
        }
    },
    methods:{
        ...mapActions('antropometri',['pushBeratBadan','setPilihanBeratBadan','setPilihanBeratBadanNoArray']),
        nextPage(){
            // this.setPilihanBeratBadan()
            this.setPilihanBeratBadanNoArray(this.inputtedBeratBadan)
            this.$router.push('/measurement/height')
        },
        modalToggle(){
            this.modal.show = !this.modal.show
        }
    },
    computed:{
        ...mapGetters('antropometri',['getHasilPerhitungan', 'getOrangAntropometri'])
    },
    watch:{
        inputtedBeratBadan(newValue){
            if(newValue.length <= 4){
                // this.pushBeratBadan(newValue)
            }
            else{
                this.inputtedBeratBadan = newValue.substring(0,4)
            }
        }
    },
    created(){
        this.virtualKeyboard.layout = VirtualKeyboardCustomLayout.numeric
    },
    mounted(){
        this.virtualKeyboard.selectedInput = this.$el.querySelector('input')        
    }
}
</script>

