<script>
import {HorizontalBar} from 'vue-chartjs'

export default {
    extends: HorizontalBar,
    data(){
        return{
            chartLegendPenyakit : [
                {name:'Demam Pada Anak', color:'#7189bf'},
                {name:'Maaf atau Gastritis', color:'#df7599'},
                {name:'Demam Tifoid', color:'#ffc785'},
                {name:'Cacar Air', color:'#72d6c9'},
                {name:'Lain-lain', color:'#EE7469'},
            ],
            options: {
                legend: {
                    display: false
                },
                maintainAspectRatio:false,
                responsive:true,
            },
        }
    },
    methods:{
        getLegendColor(chartLegendData){
            let colorList = []
            for(let item of chartLegendData){
                colorList.push(item.color)
            }
            return colorList
        },
        getLegendName(chartLegendData){
            let nameList = []
            for(let item of chartLegendData){
                nameList.push(item.name)
            }
            return nameList
        }
    },
    mounted(){
        let data = {
            labels:this.getLegendName(this.chartLegendPenyakit),
                datasets:[
                    {
                        data:[55,250,124,89,65],
                        backgroundColor:this.getLegendColor(this.chartLegendPenyakit),
                        borderWidth:0
                    }
                ]
        }

        this.renderChart(data, this.options)
    }
}
</script>

