<template>
    <div class="wrapper" :class="{'navigation-mode':navigationOn}">
        <div>
            <section class="text-center">
                <img id="rektor-img" src="../../assets/rektor.png" alt="">
            </section>
            <section>
                <article>
                    <div id="msg-1" class="text-center animated fadeInUp">
                        <h1>Ayo kita cari tahu</h1>
                        <p>Berapa kalori yang dibutuhkan tubuhmu</p>
                    </div>
                </article>
                <article>
                    <div id="msg-2" class="text-center animated hide">
                        <h1>Penasaran?</h1>
                        <p>Ayo sini mendekat</p>
                    </div>
                </article>
            </section>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            navigationOn:false
        }
    },
    mounted(){
        let divOne = this.$el.querySelector('#msg-1')
        let divTwo = this.$el.querySelector('#msg-2')

        setInterval(()=>{
            if(divOne.classList.contains('fadeInUp') && divTwo.classList.contains('hide')){
                divOne.classList.replace('fadeInUp','fadeOutUp')
                divOne.classList.add('hide')

                if(divTwo.classList.contains('fadeOutUp')){
                    divTwo.classList.remove('fadeOutUp')
                }

                divTwo.classList.remove('hide')
                divTwo.classList.add('fadeInUp')
            }
            else if(divTwo.classList.contains('fadeInUp') && divOne.classList.contains('hide')){
                divTwo.classList.replace('fadeInUp','fadeOutUp')
                divTwo.classList.add('hide')

                if(divOne.classList.contains('fadeOutUp')){
                    divOne.classList.remove('fadeOutUp')
                }

                divOne.classList.remove('hide')
                divOne.classList.add('fadeInUp')
            }
            
        }, 3000)
    }
}
</script>

