<template>
  <div class="container-page">
      <div class="text-center">
          <h3>Bagaimana Aktivitas Fisikmu?</h3>
          <h1 class="">
              <select v-model="selectedActivity" name="umur" id="">
                  <option value="0" selected disabled>Pilih tingkat aktivitas fisik</option>
                  <option v-for="(item, index) in activityType" :key="index" :value="item.level">{{item.text}}</option>
              </select>
            </h1>
          <p class="">Pilih yang paling sesuai</p>
      </div>

        <!-- <modal id="modal" buttonCancelText="Tidak" buttonOkText="Ya" :callbackOk="nextPage" :callbackCancel="cancel" :show="modal.show">
            <div>
                <h3> {{selectedActivityType }} </h3>
            </div>
            <h4>Apakah pilihanmu sudah benar?</h4>
        </modal> -->
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import JenisBebanAktivitias from '@/helper/JenisBebanAktivitas'
import Modal from '@/components/modal'
import NextStepButton from './ButtonNextStep'

export default {
    components:{
        Modal,
        NextStepButton
    },
    data(){
        return {
            activityType:[],
            selectedActivity:0,
            modal:{
                show:false
            }
        }
    },
    methods:{
        ...mapActions('antropometri',['setJenisAktivitas']),
        nextPage(){
            this.$router.push('/measurement/result')
        },
        cancel(){
            this.modal.show = false
        }
    },
    computed:{
        ...mapGetters('antropometri',['getHasilPerhitungan', 'getOrangAntropometri']),
        selectedActivityType(){
            let context = this
            let selected = this.activityType.find(item=>{
                return item.level == context.selectedActivity
            })

            if(selected){
                return selected.subText
            }
            else{
                return "Terjadi Kesalahan"
            }
        }
    },
    watch:{
        selectedActivity(newValue){
            // this.modal.show = true
            this.setJenisAktivitas(newValue)
            this.nextPage()
        }
    },
    created(){
        this.activityType = JenisBebanAktivitias
    }
}
</script>

