import {Client, Message} from 'paho-mqtt'
import RandomIntegerGenerator from '@/helper/RandomIntegerGenerator'

class PahoMqttHandler{

    constructor(brokerURL, topic, options={}){
        this.onConnectionSuccess = this.onConnectionSuccess.bind(this)
        this.onConnectionFailure = this.onConnectionFailure.bind(this)
        this.onConnectionLost = this.onConnectionLost.bind(this)
        this.onMessageArrived = this.onMessageArrived.bind(this)
        this.sendMessage = this.sendMessage.bind(this)

        this.subscribe = this.subscribe.bind(this)
        this.broadcast = this.broadcast.bind(this)

        this.observers = []
        this.topic = topic

        this.client = new Client(brokerURL,options.port, `${options.clientId+RandomIntegerGenerator(10,999)}`)

        this.client.onConnectionLost = this.onConnectionLost
        this.client.onMessageArrived = this.onMessageArrived
        
        this.client.connect({
            onFailure:this.onConnectionFailure,
            onSuccess:this.onConnectionSuccess,
        })
    }

    onConnectionLost(responseObject) {
        if (responseObject.errorCode !== 0) {
          console.log("onConnectionLost:"+responseObject.errorMessage);
        }
      }

    onConnectionSuccess(){
        console.log("Connected");        
        this.client.subscribe([this.topic])
        console.log(`Subscribed to ${this.topic}`);
    }

    onConnectionFailure(error){
        console.log("Something wrong");
        console.log(error);
    }

    onMessageArrived(message){
        this.broadcast(message)
    }

    sendMessage(message, topic=this.topic){
        let messagePayload = new Message(message)
        messagePayload.destinationName = topic
        this.client.send(messagePayload)
    }

    subscribe(fn){
        this.observers.push(fn)
    }

    broadcast(value){        
        if(this.observers){
            this.observers.forEach(subscriber =>{
                subscriber(value)
            })
        }
    }
    
}

export default PahoMqttHandler